table {
    border-collapse: collapse;
}
table td, table th {
    border: 1px solid white;
}
table tr:first-child th {
    border-top: 0;
}
table tr:last-child td {
    border-bottom: 0;
}
table tr td:first-child,
table tr th:first-child {
    border-left: 0;
}
table tr td:last-child,
table tr th:last-child {
    border-right: 0;
}

tbody tr:nth-child(even) {
  background-color: #202020;
}

tbody tr:nth-child(odd) {
  background-color: #2d2d2d;
}



/*--- DECRYPT INPUT FORM ---------------------------------------------*/
$passPrimary: #11998e;
$passSecondary: #38ef7d;
$passWhite: #fff;
$passGray: #9b9b9b;
.form__group {
  position: relative;
  padding: 15px 0 0;
  margin-top: 10px;
  width: 50%;
}

.form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid $passGray;
  outline: 0;
  font-size: 1.3rem;
  color: $passWhite;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;

  &::placeholder {
    color: transparent;
  }

  &:placeholder-shown ~ .form__label {
    font-size: 1.3rem;
    cursor: text;
    top: 20px;
  }
}

#search-container{

    width: 100%;

}

.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: $passGray;
}

.form__field:focus {
  ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: $passPrimary;
    font-weight:700;    
  }
  padding-bottom: 6px;  
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, $passPrimary,$passSecondary);
  border-image-slice: 1;
}
/* reset input */
.form__field{
  &:required,&:invalid { box-shadow:none; }
}




/*--- LINKS -----------------------------------------------------------------*/

a {  
	text-decoration: none;
	color: $link-color; 
    cursor: pointer;
	/*&:visited { 
		color: lighten($link-color, 20); 
	}*/

	&:hover { 
		color: lighten($link-color, 20); 
	}

	&:focus { 
		outline: thin dotted; 
		color: darken($link-color, 20);
	}

	&:hover, 
	&:active { 
		outline: 0; 
	}
}

a.blank-link{
	color: #fff;
	&:hover{
		color: darken(#fff,20);
		}
	&:focus { 
		outline: thin dotted; 
		color: darken(#fff, 20);
        }
        &:hover, 
        &:active { 
                outline: 0; 
        }
}


#AsubmitButton{


    background-color:transparent !important;
    color: #e88484 !important;
    border-color:transparent;
    padding: 20px 0px 0px 0px;
    font-size: 17px;

}
  

/*--- BLOCKQUOTE ------------------------------------------------------------*/

blockquote {
    border-left: 4px solid $border-color;
    font-size: 14px;
    font-family: $alt-font;
    margin-left: 1em;
    margin-right: 5em;
    padding-left: 15px;
    line-height: 22px;
}

/*--- CODE FORMAT -----------------------------------------------------------*/

tt, code, kbd, samp, pre {
	font-family: $code-font;
	/*color: #66a1ff;*/
    // color:#f5c242
    color: #ffffff;
}

p > code,
a > code,
li > code,
figcaption > code,
td > code {
    margin: 1px 2px;
	/*padding: 0 5px;*/
    padding: 3px 5.5px;
    white-space: nowrap;
    font-size: 12px;
    line-height: 1.5;
    box-shadow: 0px 1px 9px 0.1px darken($base-color, 30);
    /*border: 1px solid lighten(#000, 90);*/
	/*background-color: lighten(#000, 95);*/
	background-color: lighten($body-color,15);
	border-radius: 2.5px;
	border-color: lighten($body-color,30);
}

pre {
    font-size: 12px;
	line-height: 1.5;
	overflow-x: auto;

	&::-webkit-scrollbar {
		height: 12px;
		background-color: #34362e;
		border-radius: 0 0 4px 4px;
	}

	&::-webkit-scrollbar-thumb:horizontal {
		background-color: #6a6d5d;
		border-radius: 4px;
	}
}

/*--- ABOUT SECTION (SOCIAL MEDIA) ------------------------------------------*/

.personal-social-media {
    word-spacing: 10px;
    
    img:hover {
        opacity: 0.5;
    }
}

/*--- DIVIDING LINE ---------------------------------------------------------*/

hr { 
    display: block; 
    margin: 1em 0 1.5em; 
    padding: 0;
    height: 1px; 
    border: 0; 
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #fff; 
}

/*--- FIGURES & IMAGES ------------------------------------------------------*/

figure {
  margin: 0;
  padding-top: 10px;
  padding-bottom: 10px;

  img {
    margin-bottom: 0px;
  }
}

svg:not(:root) {
  overflow: hidden; 
}

/*--- PAGINATION ------------------------------------------------------------*/

.pagination {
    a{
        color: lighten($base-color,30);
    }
    
    a:hover {
        color: lighten($base-color,60);
    }
    
    word-spacing: 10px;
}

/*--- EQUATIONS -------------------------------------------------------------*/

.has-jax {
    font: inherit; 
    font-size: 100; 
    background: inherit; 
    border: inherit;
    color: #006699;
}

/*--- NAVIGATION BAR --------------------------------------------------------*/

.navigation-bar {
    //word-spacing: 20px;
    list-style-type: none;
    margin: 0;
    padding: 0;
    text-align: center;

    li {
	    padding-left: 10px;
	    padding-right: 10px;
	    display: inline-block;
    }

    a{
    	text-transform: uppercase;
        //color: $base-color;
        color: lighten($base-color,50);
        //color: white;
        font-weight: 100;
    }
    
    a:hover {
    	text-transform: uppercase;
        color: lighten($base-color,100);
        font-weight: 100;
    }
}

/*--- TAGS ------------------------------------------------------------------*/

.inline-tags {
    font-size: 14px;

    a {
        color: #F2992E;
    }
    
    &:hover {
        color: lighten(#F2992E,20);
    }
}

/*--- GLOBAL TRANSITION -----------------------------------------------------*/

b, i, strong, em, blockquote, p, q, span, figure, img, h1, h2, header, input, a {
	transition: all 0.2s ease;
}

/*--- MAIN CONTENT ----------------------------------------------------------*/

.entry-content {
    font-size: 16px;
    line-height: 26px;
}

.entry-title {
    font-weight: 100;
}

.entry-subtitle {
	font-weight: 100;
}

.main-header-image {
    img {
        @media #{$medium} {
            margin-left: 0px;
			margin-right: 0px;
			max-width: $column-width;
		}

		@media #{$large} {
			max-width: $column-width;
		}
    }
    
    img:hover {
        opacity: 0.8;
    }

    h1, a, div {
    	text-transform: uppercase;
		color: lighten($base-color, 80);
		letter-spacing: 5px;
	}
	
	a:hover{
	    color: lighten($text-color,100);
	
	}
}

.post-date {
    font-size: 12px;
	text-transform: uppercase;
    color: lighten($base-color,50);

	a {
		color: lighten($base-color,50);
	}
    
    a:hover{
        color: lighten($base-color,20);
    }
}

header .entry-meta {
	display: none; // hide header meta on small screens

	@media #{$medium} {
		display: block;
	}
}

/*--- POST INDEX ------------------------------------------------------------*/

#post-index {
	#main {
        margin: 40px 2px 20px 2px;
        //for border around image uncomment line
        // @media #{$medium} {
        //     .featured-image {
        //             display:block;
        //             margin:-25px -15px;
        //             padding: 0px 0px 20px 0px;
        //         }
        //     }

		@media #{$medium} {
			margin-left: 20px;
			margin-right: 20px;
		}

		@media #{$large} {
			max-width: $column-width;
			margin-top: 50px;
			margin-left: auto;
			margin-right: auto;
		}
	}

	article {
		/*background-color: #fff;*/
		background-color:#202020;
		/*box-shadow: 0 0 0 0, 0 6px 12px rgba($base-color,0.2);*/
		box-shadow: 0 0 0 0,0 1px 9px rgb(0, 0, 0);
		border-radius: 3px;
		margin-bottom: 20px;
		padding: 25px 15px;
		overflow: hidden;

