		.featured-image {
				display:block;
	        	margin:-25px -15px;
	        	padding: 0px 0px 20px 0px;
	        }
	    }

		@media #{$medium} {
			padding: 30px;

			.featured-image {
				display:block;
	        	margin:-30px -30px;
	        	padding: 0px 0px 30px 0px;
	        }
		}

		@media #{$large} {
			margin-bottom: 30px;
			padding: 50px 80px;

			.featured-image {
				display:block;
	        	margin:-50px -80px;
	        	padding: 0px 0px 50px 0px;
	        }
		}
       
        a { 

            img:hover {
                opacity: 0.7;
            }
        }

        
    }
   
}

/*--- FOOTER ----------------------------------------------------------------*/

.footer-wrapper {
	margin: 2em auto;
	text-align: center;
	color: lighten($text-color,20);
    font-size: 14px;

    a:link{
        color: lighten($text-color,60);
    }
    
	a:hover {
		color: lighten($text-color,80);
	}
}

/*--- BROWSER UPGRADE -------------------------------------------------------*/

.upgrade {
	padding: 10px;
	text-align: center;
}

/*--- BODY ------------------------------------------------------------------*/

body {
    font-family: $base-font;
    color: $text-color;
	margin: 0;
	padding: 0;
	width: 100%;
	background-color: $body-color;
    margin-top: 0px + $doc-line-height;
    font-size: 0px + $doc-font-size;
}

/*--- HEADINGS --------------------------------------------------------------*/

h1, h2, h3, h4, h5, h6 {
	font-family: $heading-font;
}

h1 {
	@media #{$medium} {
        font-size: 32px;
	}
}

/*--- Progress Bar ----------------------------------------------------------*/
.progress{
    background-color:lime;
    position: fixed;
    top: 0;
    left: -50px;
    width:0%;
    height:4px;
    z-index: 1000;
}